.main {
    .championCard {
        cursor: pointer;
        background-color: rgb(9, 78, 0);
        border: solid 0.3rem lightblue;
        margin:1rem;
        width:10rem;
        height:12rem;
        display: flex;
        flex-direction: column;
        h1 {
            border-top: solid 0.3rem lightblue;
            font-weight: normal;
            font-size: 1rem;
            text-align: center;
            background-color: rgb(9, 78, 0);
        }
        .championIcon {
            height: 10rem;
            width: 10rem;
        }
        .description {
            background-color: rgb(9, 78, 0);
            display: none;
            align-items: center;
            h2 {
                font-size: 1rem;
            }
            .roleIcon {
                height:2rem;
            }
        }
        &:hover {
            transition: 0.3s ease;
            h1 {
                transform: translateY(-2.2rem);
                transition: 0.3s ease;                
            }
            .description{
                border-top: solid 0.3rem lightblue;
                transform: translateY(-2.2rem);
                display: flex;
                justify-content: space-between;
                .roleIcon {
                    background-color: grey;
                }
            }
        }
    }
    .none {
        display: none;
    }
}