.roleSelection {
    padding:1rem;
    cursor: pointer;
    margin:1rem;
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(5, 43, 0);
    width: 10rem;
    height: 3rem;
    img {
        height:1rem;
    }
    h1 {
        padding-left: 0.3rem;
        font-size: 1rem;
    }
    &.active {
        border: solid 0.2rem lightblue;
        width:9.6rem;
        height:2.6rem;

    }
    &:hover {
        border: solid 0.2rem lightblue;
        width:9.6rem;
        height:2.6rem;
    }
}