.main {
    .championInfo {
        margin: 0 auto;
        width: 80%;
        height: 100vh;
        background-color: rgb(7, 114, 202);
        .top {
            padding-top:1rem;
            margin-bottom:2rem;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
        }
        .bot {
            display: flex;
            flex-direction: row-reverse;
            p {
                margin-left:3rem;
                width: 25rem;
                font-size: 1.1rem;
            }
        }
        img {
            //margin:0 auto;
            width: 8rem;
            border-radius: 50%;
        }
        .hide {
            display:none;
            width:1rem;
            height:1rem;
        }
        .video {
            margin:0 2rem 0 auto;
            border: solid 0.5rem black;
        }
    }
}