.socials {
    border-top: 0.2rem solid rgb(1, 48, 1);
    border-bottom: 0.2rem solid rgb(1, 48, 1);
    background-color: darkgreen;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    a:any-link {
        text-decoration: none;
        color: black;
    }
    .socialIcon {
        width:2.5rem;
        height:2.5rem;
        margin: 1rem 2rem;
        cursor: pointer;
        font-size: 2.5rem;
        &:hover{
            color:white;
        }
    }
}